@tailwind base;
@tailwind components;
@tailwind utilities;

@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

html {
  scroll-behavior: smooth;
}

body {
  background: #ddd;
  color: #4c4d4d;
  font-family: Poppins, sans-serif;
}
section {
  overflow: hidden;
}
.modal-active {
  overflow: hidden;
}
.welcome-mask {
  left: 0;
  top: 0;
  position: fixed;
  z-index: 99;
  width: 100%;
  height: 100%;
}
.welcome-mask.remove {
  display: none;
}
.hero-bg {
  background-image: url(../public/media/pexels-lukas-317371.jpg);
}

.testimonial-slider .slick-slide {
  padding: 0 0.75rem;
}

*:focus-visible {
  outline: none;
}
.fly-in-text.hidden {
  display: block;
}
.fly-in-text {
  list-style: none;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
  width: 90%;
  text-align: center;
}

.fly-in-text li {
  display: inline-block;
  margin-right: 50px;
  font-weight: 300;
  opacity: 1;
  transition: all 1s ease;
}

.fly-in-text.remove {
  opacity: 0;
}

.fly-in-text li {
  margin-right: 20px;
  font-weight: 300;
  font-size: 2em;
}

@media (min-width: 768px) {
  .fly-in-text li {
    margin-right: 30px;
    font-size: 3em;
  }
}

.fly-in-text li:last-child {
  margin-right: 0;
}

.fly-in-text.hidden li {
  opacity: 0;
}

.fly-in-text.hidden li:nth-child(1) {
  transform: translateX(-200px) translateY(-200px);
}
.fly-in-text.hidden li:nth-child(2) {
  transform: translateX(20px) translateY(100px);
}
.fly-in-text.hidden li:nth-child(3) {
  transform: translateX(-150px) translateY(-80px);
}
.fly-in-text.hidden li:nth-child(4) {
  transform: translateX(10px) translateY(-200px);
}
.fly-in-text.hidden li:nth-child(5) {
  transform: translateX(-300px) translateY(200px);
}
.fly-in-text.hidden li:nth-child(6) {
  transform: translateX(80px) translateY(-20px);
}
.fly-in-text.hidden li:nth-child(4) {
  transform: translateX(30px) translateY(200px);
}
