.portfolio-overlay {
  background: rgba(0, 0, 0, 0.7);
  bottom: 0;
  display: flex !important;
  left: 0;
  opacity: 0;
  position: absolute;
  right: 0;
  text-align: center;
  top: 0;
  transition: all 0.3s ease-in-out;
}

.portfolio-box:hover .portfolio-overlay {
  opacity: 1;
}
